function getStudyValue(w) {
  // Canonical field
  const raw = w && typeof w === "object" ? w.studied ?? 0 : 0;
  const n = Number(raw);
  return Number.isFinite(n) && n >= 0 ? n : 0;
}

function getLastStudyTime(w) {
  const raw = w && typeof w === "object" ? w.lastStudyTime ?? 0 : 0;
  const n = Number(raw);
  return Number.isFinite(n) && n >= 0 ? n : 0; // default 0 => very old
}

function sortWords(words, sortType) {
  if (sortType === "asc") {
    const groups = new Map();
    words.forEach((w) => {
      const len = (w.jp_word || "").length;
      if (!groups.has(len)) groups.set(len, []);
      groups.get(len).push(w);
    });
    const keys = Array.from(groups.keys()).sort((a, b) => a - b);
    const result = [];
    keys.forEach((k) => {
      result.push(...shuffleArray(groups.get(k)));
    });
    return result;
  }
  return shuffleArray(words);
}

function uniqueByWordKey(words) {
  const seen = new Set();
  const result = [];
  for (const w of words || []) {
    if (!w) continue;
    const key = `${w.jp_word || ""}|${w.kanji_jp_word || ""}`;
    if (seen.has(key)) continue;
    seen.add(key);
    result.push(w);
  }
  return result;
}

export default function WordTest() {
  const navigate = useNavigate();
  const { state, dispatch } = useApp();
  const { notebooks, currentNotebookId } = state.shared;
  const { playbackOptions, playbackSpeed, wordType } = state.systemSettings;

  const [config, setConfig] = useState(defaultConfig);
  // Stage: 'new' | 'review' | 'test' | 'wrong'
  const [stage, setStage] = useState("new");
  const [restored, setRestored] = useState(false);
  const [attemptedRestore, setAttemptedRestore] = useState(false);

  const currentNotebook = useMemo(
    () => notebooks.find((n) => n.id === currentNotebookId),
    [notebooks, currentNotebookId]
  );

  // Pools for Stage 3/4
  const [sessionPool, setSessionPool] = useState(() => new Set()); // union of learned ids in stage 1+2
  const [wrongSet, setWrongSet] = useState(() => new Set()); // wrong ids collected in Stage 3

  const eligibleWords = useMemo(() => {
    const ctx =
      currentNotebook && Array.isArray(currentNotebook.context)
        ? currentNotebook.context
        : [];
    if (stage === "new") {
      const filtered = ctx.filter((w) => {
        if (!w || !w.jp_word) return false;
        return getStudyValue(w) === 0; // treat null/undefined/NaN as 0
      });
      const uniq = uniqueByWordKey(filtered);
      return uniq.slice(0, Math.max(0, config.max_word_study));
    }
    // review: pick studied > 0 AND past review interval, then choose the lowest-studied items up to max_word_study
    const now = Date.now();
    const hours = Number(
      config.review_interval ?? defaultConfig.review_interval
    );
    const clampHours = isNaN(hours)
      ? defaultConfig.review_interval
      : Math.max(0.05, hours);
    const thresholdMs = clampHours * 3600 * 1000;
    const studiedItems = ctx.filter((w) => {
      if (!w || !w.jp_word) return false;
      if (!(getStudyValue(w) > 0)) return false;
      const last = getLastStudyTime(w);
      return now - last > thresholdMs;
    });
    const uniqStudied = uniqueByWordKey(studiedItems);
    if (uniqStudied.length <= config.max_word_study) return uniqStudied;
    const groups = new Map();
    for (const w of uniqStudied) {
      const s = getStudyValue(w);
      if (!groups.has(s)) groups.set(s, []);
      groups.get(s).push(w);
    }
    const keys = Array.from(groups.keys()).sort((a, b) => a - b);
    const picked = [];
    for (const k of keys) {
      const arr = shuffleArray(groups.get(k));
      for (const w of arr) {
        picked.push(w);
        if (picked.length >= config.max_word_study) return picked;
      }
    }
    return picked;
  }, [currentNotebook, config.max_word_study, config.review_interval, stage]);

  const testWords = useMemo(() => {
    if (stage !== "test") return [];
    const ctx =
      currentNotebook && Array.isArray(currentNotebook.context)
        ? currentNotebook.context
        : [];
    const allowed = new Set(sessionPool);
    const arr = ctx.filter((w) => allowed.has(w.id));
    return uniqueByWordKey(arr).slice();
  }, [stage, sessionPool, currentNotebook]);

  const wrongWords = useMemo(() => {
    if (stage !== "wrong") return [];
    const ctx =
      currentNotebook && Array.isArray(currentNotebook.context)
        ? currentNotebook.context
        : [];
    const allowed = new Set(wrongSet);
    const arr = ctx.filter((w) => allowed.has(w.id));
    return uniqueByWordKey(arr).slice();
  }, [stage, wrongSet, currentNotebook]);

  // Build a map for lookup and a default order
  const pageWords =
    stage === "test"
      ? testWords
      : stage === "wrong"
      ? wrongWords
      : eligibleWords;
  const byId = useMemo(
    () => new Map((pageWords || []).map((w) => [w.id, w])),
    [pageWords]
  );
  const defaultOrderWords = useMemo(() => {
    return sortWords(pageWords, config.sort_type);
  }, [pageWords, config.sort_type]);
  const defaultAllIds = useMemo(
    () => defaultOrderWords.map((w) => w.id),
    [defaultOrderWords]
  );
  // Allow restoring a persisted order instead of recomputing (prevents shuffle mismatch)
  const [overrideAllIds, setOverrideAllIds] = useState(null);
  const allIds = useMemo(() => {
    const ids = overrideAllIds || defaultAllIds;
    // Filter out ids that no longer exist in current context, and deduplicate defensively
    return Array.from(new Set(ids.filter((id) => byId.has(id))));
  }, [overrideAllIds, defaultAllIds, byId]);

  // If a restored order becomes invalid (e.g., no words in current stage),
  // fall back to the freshly computed default order so remaining new cards appear.
  useEffect(() => {
    if (overrideAllIds && allIds.length === 0 && defaultAllIds.length > 0) {
      setOverrideAllIds(null);
    }
  }, [overrideAllIds, allIds.length, defaultAllIds.length]);

  const [round, setRound] = useState(0);
  const [wordIndex, setWordIndex] = useState(0);
  const slicesCount = useMemo(
    () => Math.ceil(allIds.length / Math.max(1, config.slice_length)),
    [allIds.length, config.slice_length]
  );
  const currentSliceNo = useMemo(
    () =>
      allIds.length === 0
        ? 0
        : Math.floor(wordIndex / Math.max(1, config.slice_length)) + 1,
    [wordIndex, allIds.length, config.slice_length]
  );

  const [sliceIds, setSliceIds] = useState([]);
  const [currentQueue, setCurrentQueue] = useState([]);
  const [queueIdx, setQueueIdx] = useState(0);
  const [memorySet, setMemorySet] = useState(() => new Set());
  const [visitedSet, setVisitedSet] = useState(() => new Set());

  const [showExitConfirm, setShowExitConfirm] = useState(false);
  const [showFinishModal, setShowFinishModal] = useState(false);
  const [finishMessage, setFinishMessage] = useState(
    " +1"
  );
  const [showQueues, setShowQueues] = useState(false);
  const [showLocalSettings, setShowLocalSettings] = useState(false);
  const [draftConfig, setDraftConfig] = useState(defaultConfig);
  const [showClearConfirm, setShowClearConfirm] = useState(false);
  const [clearing, setClearing] = useState(false);
  const [showHiragana, setShowHiragana] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [isAnswerVisible, setIsAnswerVisible] = useState(false);
  // Prevent re-triggering end-of-session logic once finished
  const [sessionDone, setSessionDone] = useState(false);

  // switch stage helper (reset state and re-attempt restore)
  const switchStage = useCallback((nextStage) => {
    setStage(nextStage);
    setRestored(false);
    setAttemptedRestore(false);
    setRound(0);
    setWordIndex(0);
    setSliceIds([]);
    setCurrentQueue([]);
    setQueueIdx(0);
    setMemorySet(new Set());
    setVisitedSet(new Set());
    setOverrideAllIds(null);
    setIsAnswerVisible(false);
    setSessionDone(false);
  }, []);

  const handleClearAllStudy = useCallback(async () => {
    if (!currentNotebookId || !currentNotebook) return;
    try {
      setClearing(true);
      for (const w of currentNotebook.context || []) {
        if (!w || !w.id || !w.jp_word) continue;
        await notebookService.updateWordInNotebook(currentNotebookId, w.id, {
          studied: 0,
        });
        dispatch(
          updateWordInNotebook(currentNotebookId, w.id, {
            studied: 0,
          })
        );
      }
      // Also clear persisted WordTest progress
      try {
        notebookService.updateNotebookWordTest(currentNotebookId, null);
      } catch (_) {}
    } catch (e) {
      console.error("", e);
    } finally {
      setClearing(false);
      setShowClearConfirm(false);
    }
  }, [currentNotebookId, currentNotebook, dispatch]);

  const loadSlice = useCallback(
    (startIdx, idsOrder = allIds) => {
      const start = Math.max(0, startIdx);
      const end = Math.min(
        idsOrder.length,
        start + Math.max(1, config.slice_length)
      );
      const ids = Array.from(new Set(idsOrder.slice(start, end)));
      setSliceIds(ids);
      setMemorySet(new Set());
      setCurrentQueue(shuffleArray(ids));
      setQueueIdx(0);
    },
    [allIds, config.slice_length]
  );

  useEffect(() => {
    if (!attemptedRestore) return; // wait until restore attempt completes
    if (restored) return; // skip auto-reset if we've restored a session
    setRound(0);
    setWordIndex(0);
    setVisitedSet(new Set());
    loadSlice(0, allIds);
  }, [allIds, loadSlice, restored, attemptedRestore]);

  useEffect(() => {
    if (
      allIds.length > 0 &&
      sliceIds.length === 0 &&
      currentQueue.length === 0
    ) {
      loadSlice(0, allIds);
    }
  }, [allIds, sliceIds.length, currentQueue.length, loadSlice]);

  const currentId = currentQueue[queueIdx];
  const currentWord = byId.get(currentId);

  const { playSequence } = useAnswerPlayback({
    result: null,
    question: currentWord,
    onNext: () => {},
    playbackOptions,
    rate: playbackSpeed,
  });

  const speakManually = useCallback(
    (text, lang) => {
      const options = {};
      if (lang === "ja") {
        options.jp = true;
        playSequence(null, { jp_word: text }, options, { skipSound: true });
      } else if (lang === "zh") {
        options.ch = true;
        playSequence(null, { ch_word: text }, options, { skipSound: true });
      }
    },
    [playSequence]
  );

  // Auto play the question word when a new item appears
  useEffect(() => {
    if (!currentWord || !currentWord.jp_word) return;
    speakManually(currentWord.jp_word, "ja");
  }, [currentId]);

  const onRemember = useCallback(() => {
    if (!currentId) return;
    setIsAnswerVisible(false);
    setVisitedSet((prev) => new Set(prev).add(currentId));
    if (stage === "new" || stage === "review") {
      setMemorySet((prev) => new Set(prev).add(currentId));
    } else if (stage === "wrong") {
      // mark learned and remove from wrong set
      setMemorySet((prev) => new Set(prev).add(currentId));
      setWrongSet((prev) => {
        const n = new Set(prev);
        n.delete(currentId);
        return n;
      });
    } else {
      // test: only record answered
    }
    setQueueIdx((i) => i + 1);
  }, [currentId, stage]);

  const onNotYet = useCallback(() => {
    if (!currentId) return;
    setIsAnswerVisible(false);
    setVisitedSet((prev) => new Set(prev).add(currentId));
    if (stage === "test") {
      setWrongSet((prev) => new Set(prev).add(currentId));
    }
    setQueueIdx((i) => i + 1);
  }, [currentId, stage]);

  // After revealing the answer, speak according to settings (same as Reading page)
  useEffect(() => {
    if (!isAnswerVisible) return;
    if (!currentWord) return;
    playSequence(null, currentWord, playbackOptions, { skipSound: true });
  }, [isAnswerVisible, currentId, currentWord, playSequence, playbackOptions]);

  useEffect(() => {
    // Stop any further progression once the session is finished
    if (sessionDone) return;
    if (queueIdx < currentQueue.length) return;
    if (currentQueue.length === 0 && sliceIds.length === 0) return;

    if (stage === "test") {
      // Present each exactly once; go to next slice or switch to wrong/finish
      const hasNextSlice = wordIndex + config.slice_length < allIds.length;
      if (hasNextSlice) {
        const nextStart = wordIndex + config.slice_length;
        setWordIndex(nextStart);
        loadSlice(nextStart, allIds);
      } else {
        if (wrongSet.size > 0) {
          // move to wrong practice
          switchStage("wrong");
        } else {
          setSessionDone(true);
          setSliceIds([]);
          setCurrentQueue([]);
          setQueueIdx(0);
          setFinishMessage("");
          setShowFinishModal(true);
        }
      }
      return;
    }

    // Wrong practice: repeat until all wrong remembered; then finish
    if (stage === "wrong") {
      const allCoveredWrong = sliceIds.every((id) => memorySet.has(id));
      if (allCoveredWrong) {
        const hasNextSlice = wordIndex + config.slice_length < allIds.length;
        if (hasNextSlice) {
          const nextStart = wordIndex + config.slice_length;
          setWordIndex(nextStart);
          loadSlice(nextStart, allIds);
        } else {
          // If wrongSet still has items (shouldn't if remembered), just reshuffle remaining
          if (wrongSet.size > 0) {
            const remaining = allIds.filter((id) => !memorySet.has(id));
            if (remaining.length > 0) {
              setSliceIds(remaining);
              setCurrentQueue(remaining);
              setQueueIdx(0);
              return;
            }
          }
          setSessionDone(true);
          setSliceIds([]);
          setCurrentQueue([]);
          setQueueIdx(0);
          setFinishMessage("");
          setShowFinishModal(true);
        }
      } else {
        const pending = sliceIds.filter((id) => !memorySet.has(id));
        setCurrentQueue(pending);
        setQueueIdx(0);
      }
      return;
    }

    const allCovered = sliceIds.every((id) => memorySet.has(id));
    if (allCovered) {
      const hasNextSlice = wordIndex + config.slice_length < allIds.length;
      if (hasNextSlice) {
        const nextStart = wordIndex + config.slice_length;
        setWordIndex(nextStart);
        loadSlice(nextStart, allIds);
      } else {
        const nextRound = round + 1;
        setRound(nextRound);
        if (nextRound >= config.round_count) {
          // Mark as finished and clear queues to avoid the effect firing again
          setSessionDone(true);
          setSliceIds([]);
          setCurrentQueue([]);
          setQueueIdx(0);
          (async () => {
            try {
              const nbId = currentNotebookId;
              for (const id of Array.from(visitedSet)) {
                const word = byId.get(id);
                if (!word) continue;
                const base = getStudyValue(word);
                const newStudy = base + 1;
                const nowTs = Date.now();
                await notebookService.updateWordInNotebook(nbId, id, {
                  studied: newStudy,
                  lastStudyTime: nowTs,
                });
                dispatch(
                  updateWordInNotebook(nbId, id, {
                    studied: newStudy,
                    lastStudyTime: nowTs,
                  })
                );
              }
              // add visited words to session pool for Stage 3 testing
              setSessionPool((prev) => {
                const n = new Set(prev);
                for (const id of Array.from(visitedSet)) n.add(id);
                return n;
              });
              setFinishMessage(" +1");
              setShowFinishModal(true);
            } catch (e) {
              console.error(" studied ", e);
              setShowFinishModal(true);
            }
          })();
        } else {
          const reshuffled = shuffleArray(allIds);
          // Ensure the entire new round uses the same order for all slices
          setOverrideAllIds(reshuffled);
          setWordIndex(0);
          setSliceIds([]);
          setCurrentQueue([]);
          setQueueIdx(0);
          setMemorySet(new Set());
          setTimeout(() => loadSlice(0, reshuffled), 0);
        }
      }
      return;
    }

    const pending = sliceIds.filter((id) => !memorySet.has(id));
    setCurrentQueue(pending);
    setQueueIdx(0);
  }, [
    queueIdx,
    currentQueue.length,
    sliceIds,
    memorySet,
    wordIndex,
    allIds,
    config.slice_length,
    round,
    config.round_count,
    currentNotebookId,
    byId,
    dispatch,
    loadSlice,
    visitedSet,
    sessionDone,
    stage,
    wrongSet,
    switchStage,
  ]);

  const progressText = useMemo(() => {
    const inSliceTotal = sliceIds.length;
    const inSliceDone = Math.min(queueIdx, inSliceTotal);
    return ` ${currentSliceNo}/${slicesCount}    ${inSliceDone}/${inSliceTotal}   ${
      round + 1
    }/${config.round_count}`;
  }, [
    queueIdx,
    sliceIds.length,
    currentSliceNo,
    slicesCount,
    round,
    config.round_count,
  ]);

  const confirmExit = useCallback(() => setShowExitConfirm(true), []);
  const handleExit = useCallback(() => {
    setShowExitConfirm(false);
    navigate("/");
  }, [navigate]);

  const handleFinishAndExit = useCallback(() => {
    try {
      if (currentNotebookId) {
        // Clear the saved progress after a completed session
        notebookService.updateNotebookWordTest(currentNotebookId, null);
      }
    } catch (_) {}
    setShowFinishModal(false);
    setSessionDone(false);
    navigate("/");
  }, [currentNotebookId, navigate]);

  // Attempt to restore saved WordTest progress from the notebook
  useEffect(() => {
    if (!currentNotebookId) return;
    if (attemptedRestore || restored) return;
    try {
      const nb = notebookService.getNotebook(currentNotebookId);
      const saved = nb && nb.word_test;
      if (!saved || typeof saved !== "object") {
        setAttemptedRestore(true);
        return;
      }
      if (
        saved.schemaVersion !== 1 &&
        saved.schemaVersion !== 2 &&
        saved.schemaVersion !== 3
      ) {
        setAttemptedRestore(true);
        return;
      }
      const savedStage = saved.stage || "new";
      if (savedStage !== stage) {
        setAttemptedRestore(true);
        return;
      }
      // Align config if needed, and honor saved order to avoid shuffle mismatches
      if (saved.config) setConfig(saved.config);
      if (Array.isArray(saved.allIds) && saved.allIds.length > 0) {
        setOverrideAllIds(saved.allIds);
      }
      if (Array.isArray(saved.sessionPool))
        setSessionPool(new Set(saved.sessionPool));
      if (Array.isArray(saved.wrongSet)) setWrongSet(new Set(saved.wrongSet));
      setRound(saved.round || 0);
      setWordIndex(saved.wordIndex || 0);
      setSliceIds(saved.sliceIds || []);
      setCurrentQueue(saved.currentQueue || []);
      setQueueIdx(saved.queueIdx || 0);
      setMemorySet(new Set(saved.memorySet || []));
      setVisitedSet(new Set(saved.visitedSet || []));
      setShowHiragana(!!saved.showHiragana);
      if (typeof saved.isAnswerVisible === "boolean") {
        setIsAnswerVisible(saved.isAnswerVisible);
      }
      setRestored(true);
      setAttemptedRestore(true);
    } catch (e) {
      // ignore restore errors
      setAttemptedRestore(true);
    }
  }, [currentNotebookId, attemptedRestore, restored, stage]);

  // Auto-save WordTest progress to the notebook's word_test field
  useEffect(() => {
    if (!currentNotebookId) return;
    if (allIds.length === 0) return;
    try {
      const payload = {
        schemaVersion: 3,
        notebookId: currentNotebookId,
        stage,
        allIds,
        config,
        round,
        wordIndex,
        sliceIds,
        currentQueue,
        queueIdx,
        memorySet: Array.from(memorySet),
        visitedSet: Array.from(visitedSet),
        sessionPool: Array.from(sessionPool),
        wrongSet: Array.from(wrongSet),
        showHiragana,
        isAnswerVisible,
        savedAt: Date.now(),
      };
      notebookService.updateNotebookWordTest(currentNotebookId, payload);
    } catch (e) {
      // noop
    }
  }, [
    currentNotebookId,
    stage,
    allIds,
    config,
    round,
    wordIndex,
    sliceIds,
    currentQueue,
    queueIdx,
    memorySet,
    visitedSet,
    sessionPool,
    wrongSet,
    showHiragana,
    isAnswerVisible,
  ]);

  if (!currentNotebookId || !currentNotebook) {
    return (
      <AppContainer>
        <BackPage onClick={() => navigate("/")}></BackPage>
        <Title></Title>
        <div></div>
      </AppContainer>
    );
  }

  if (allIds.length === 0) {
    return (
      <AppContainer>
        <IconContainer>
          <IconGroup>
            <SettingsToggle onClick={() => setShowSettings((s) => !s)}>
              
            </SettingsToggle>
            <HomeIcon onClick={() => setShowExitConfirm(true)}></HomeIcon>
            <LogToggle onClick={() => setShowQueues((v) => !v)}></LogToggle>
            <InfoToggle onClick={() => setShowQueues((v) => !v)}></InfoToggle>
            <BackPage onClick={() => navigate("/")}></BackPage>
          </IconGroup>
        </IconContainer>
        <Title></Title>
        <StageToggleWrap>
          <StageBtn active={stage === "new"} onClick={() => switchStage("new")}>
            
          </StageBtn>
          <StageBtn
            active={stage === "review"}
            onClick={() => switchStage("review")}
          >
            
          </StageBtn>
          <StageBtn
            active={stage === "test"}
            onClick={() => switchStage("test")}
          >
            
          </StageBtn>
          <StageBtn
            active={stage === "wrong"}
            onClick={() => switchStage("wrong")}
          >
            
          </StageBtn>
        </StageToggleWrap>
        <div>
          {stage === "new"
            ? "studied == 0"
            : stage === "review"
            ? "studied > 0"
            : stage === "test"
            ? ""
            : ""}
        </div>
      </AppContainer>
    );
  }

  return (
    <AppContainer>
      <IconContainer>
        <IconGroup>
          <SettingsToggle
            onClick={() => {
              setDraftConfig(config);
              setShowSettings((s) => !s);
            }}
          >
            
          </SettingsToggle>
          <HomeIcon onClick={confirmExit}></HomeIcon>
          <LogToggle onClick={() => setShowQueues((v) => !v)}></LogToggle>
          <InfoToggle onClick={() => setShowQueues((v) => !v)}></InfoToggle>
          <BackPage onClick={() => navigate("/")}></BackPage>
        </IconGroup>
      </IconContainer>
      <Title></Title>
      <StageToggleWrap>
        <StageBtn active={stage === "new"} onClick={() => switchStage("new")}>
          
        </StageBtn>
        <StageBtn
          active={stage === "review"}
          onClick={() => switchStage("review")}
        >
          
        </StageBtn>
        <StageBtn active={stage === "test"} onClick={() => switchStage("test")}>
          
        </StageBtn>
        <StageBtn
          active={stage === "wrong"}
          onClick={() => switchStage("wrong")}
        >
          
        </StageBtn>
      </StageToggleWrap>
      <Progress>{progressText}</Progress>

      {/*  FillIn  */}
      <div style={{ display: "flex", justifyContent: "flex-start" }}>
        <AudioRecorderPage triggerReset={currentId} />
      </div>

      {currentWord ? (
        <CardContainer
          onClick={() => !isAnswerVisible && setIsAnswerVisible(true)}
        >
          {wordType === "jp_word" && (
            <>
              <HiraganaToggleContainer>
                <ToggleButton onClick={() => setShowHiragana((v) => !v)}>
                  {showHiragana ? "" : ""}
                </ToggleButton>
              </HiraganaToggleContainer>
              {showHiragana && (
                <HiraganaTextContainer>
                  <HiraganaText>{currentWord.kanji_jp_word}</HiraganaText>
                </HiraganaTextContainer>
              )}
            </>
          )}

          {currentWord.kanji_jp_word && wordType === "kanji_jp_word" && (
            <>
              <HiraganaToggleContainer>
                <ToggleButton onClick={() => setShowHiragana((v) => !v)}>
                  {showHiragana ? "/" : "/"}
                </ToggleButton>
              </HiraganaToggleContainer>
              {showHiragana && (
                <HiraganaTextContainer>
                  <HiraganaText>{currentWord.jp_word}</HiraganaText>
                </HiraganaTextContainer>
              )}
            </>
          )}

          <WordContainer>
            {wordType === "kanji_jp_word" && (
              <span>{currentWord.kanji_jp_word || currentWord.jp_word}</span>
            )}
            {wordType === "jp_word" && <span>{currentWord.jp_word}</span>}
            {wordType === "jp_context" && (
              <span>
                {(currentWord.jp_context || []).map((part, index) =>
                  part.kanji ? (
                    <ruby key={index}>
                      {part.kanji}
                      <rt>{part.hiragana}</rt>
                    </ruby>
                  ) : (
                    <span key={index}>{part.hiragana}</span>
                  )
                )}
              </span>
            )}
            <SpeakButton
              onClick={(e) => {
                e.stopPropagation();
                speakManually(currentWord.jp_word, "ja");
              }}
            >
              
            </SpeakButton>
          </WordContainer>

          {isAnswerVisible && (
            <ResultContainer>
              <SubCard>
                <AnswerText correct>
                  {currentWord.ch_word} [{currentWord.type}]
                </AnswerText>
              </SubCard>
              <div style={{ display: "flex", justifyContent: "flex-end" }}>
                <ProficiencyButton
                  className={currentWord.word_bug ? "active" : ""}
                  onClick={async (e) => {
                    e.stopPropagation();
                    try {
                      const newVal = !currentWord.word_bug;
                      await notebookService.updateWordInNotebook(
                        currentNotebookId,
                        currentId,
                        { word_bug: newVal }
                      );
                      dispatch(
                        updateWordInNotebook(currentNotebookId, currentId, {
                          word_bug: newVal,
                        })
                      );
                    } catch (e) {
                      console.error("toggle bug flag failed", e);
                    }
                  }}
                  title="/"
                >
                  
                </ProficiencyButton>
              </div>
              <ExampleSentence
                jp_ex={currentWord.jp_ex_statement}
                ch_ex={currentWord.ch_ex_statement}
                speak={speakManually}
                jp_ex_context={currentWord.jp_ex_statement_context}
              />

              <div style={{ display: "flex", gap: 8, marginTop: 8 }}>
                <NextButton
                  onClick={(e) => {
                    e.stopPropagation();
                    onNotYet();
                  }}
                  style={{ borderColor: "#ccc" }}
                >
                  
                </NextButton>
                <NextButton
                  onClick={(e) => {
                    e.stopPropagation();
                    onRemember();
                  }}
                >
                  
                </NextButton>
              </div>
            </ResultContainer>
          )}
        </CardContainer>
      ) : (
        <GameBox>
          <div></div>
        </GameBox>
      )}

      {showQueues && (
        <PanelGrid>
          <Panel>
            <PanelTitle>current_queue</PanelTitle>
            <List>
              {currentQueue.slice(queueIdx).map((id) => {
                const w = byId.get(id);
                return (
                  <ListItem key={id}>
                    {w ? `${w.jp_word}  ${w.ch_word}` : id}
                  </ListItem>
                );
              })}
            </List>
          </Panel>
          <Panel>
            <PanelTitle>memory_queue</PanelTitle>
            <List>
              {sliceIds
                .filter((id) => memorySet.has(id))
                .map((id) => {
                  const w = byId.get(id);
                  return (
                    <ListItem key={id}>
                      {w ? `${w.jp_word}  ${w.ch_word}` : id}
                    </ListItem>
                  );
                })}
            </List>
          </Panel>
        </PanelGrid>
      )}

      {(showSettings || showLocalSettings) && (
        <>
          <Overlay
            onClick={() => {
              setShowSettings(false);
              setShowLocalSettings(false);
            }}
          />
          <FloatingSettingsPanel>
            <div style={{ padding: 12 }}>
              <SettingsPanel
                context="wordtest"
                wordTestConfig={draftConfig}
                onWordTestConfigChange={(cfg) => setDraftConfig(cfg)}
              />
              <div
                style={{
                  display: "flex",
                  gap: 8,
                  marginTop: 12,
                  justifyContent: "flex-end",
                }}
              >
                <Btn
                  onClick={() => setShowClearConfirm(true)}
                  style={{
                    background: "#fff0f0",
                    color: "#b00020",
                    borderColor: "#f3c2c2",
                  }}
                >
                  studied  0
                </Btn>
                <Btn
                  onClick={() => {
                    setShowSettings(false);
                    setShowLocalSettings(false);
                  }}
                >
                  
                </Btn>
                <Btn
                  primary
                  onClick={() => {
                    setConfig(draftConfig);
                    setRound(0);
                    setWordIndex(0);
                    setSliceIds([]);
                    setCurrentQueue([]);
                    setQueueIdx(0);
                    setMemorySet(new Set());
                    setVisitedSet(new Set());
                    setSessionDone(false);
                    setOverrideAllIds(null); // recalc order for new settings
                    setTimeout(() => {
                      const start = 0;
                      const end = Math.min(
                        (overrideAllIds || defaultAllIds).length,
                        start + Math.max(1, draftConfig.slice_length)
                      );
                      const sourceIds = overrideAllIds || defaultAllIds;
                      const ids = Array.from(
                        new Set(sourceIds.slice(start, end))
                      );
                      setSliceIds(ids);
                      setMemorySet(new Set());
                      setCurrentQueue(shuffleArray(ids));
                      setQueueIdx(0);
                    }, 0);
                    setShowSettings(false);
                    setShowLocalSettings(false);
                  }}
                >
                  
                </Btn>
              </div>
            </div>
          </FloatingSettingsPanel>
        </>
      )}

      {showClearConfirm && (
        <>
          <Overlay onClick={() => setShowClearConfirm(false)} />
          <Modal
            message={
              clearing
                ? ""
                : " studied "
            }
            onConfirm={() => {
              if (!clearing) handleClearAllStudy();
            }}
            onCancel={() => {
              if (!clearing) setShowClearConfirm(false);
            }}
            isVisible
          />
        </>
      )}

      {showFinishModal && (
        <>
          <Overlay onClick={() => setShowFinishModal(false)} />
          <Modal
            message={finishMessage}
            onConfirm={handleFinishAndExit}
            onCancel={() => setShowFinishModal(false)}
            isVisible
          />
        </>
      )}

      {showExitConfirm && (
        <>
          <Overlay onClick={() => setShowExitConfirm(false)} />
          <Modal
            message=""
            onConfirm={handleExit}
            onCancel={() => setShowExitConfirm(false)}
            isVisible
          />
        </>
      )}
    </AppContainer>
  );
}
